.lds{
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  .lds:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid rgb(30, 126, 174);
    border-color: rgb(30, 126, 174) transparent  rgb(30, 126, 174) transparent;
    animation: lds 1.2s linear infinite;
  }
  @keyframes lds {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }